

import { _decorator, Component, Node,Sprite,assetManager,Texture2D,SpriteFrame,ImageAsset,sp } from 'cc';
import FileTools from './Tool/FileTools';
import { Html5 } from './Tool/Html5';
import EventManager from './EventManager';
import {EventType, EventData} from './Define'


const { ccclass, property } = _decorator;

@ccclass('Machine')
export class Machine extends Component {

  @property(Sprite)
  sp: Sprite = null!;

  @property(sp.Skeleton)
  skeleton: sp.Skeleton = null!;

  spineJson :any = null;
  spineAtlas : any = null;
  spineTexture: any = null;




  start() {

    EventManager.getInstance().on('machine', this.onEvent, this);
    
  }


  onEvent (data: EventData) {
    console.log('[Machine]收到了事件消息_type:',data.eventType)

    let _eventType = data.eventType;
    let skeleton = this.skeleton;

    if(_eventType == EventType.REFRESH_SKELETON) {
      skeleton.skeletonData = data.param.skeletonData;
      skeleton.animation = 'beiji';

      console.log('onEvent.skeleton:',skeleton);
    }
    else if(_eventType == EventType.CHANGE_SPINE){

    }
    else if(_eventType == EventType.CHANGE_SPINE_ANIMATION){

    }
    else if(_eventType == EventType.UPDATE_SPINE_SKIN){

    }
    else if(_eventType == EventType.OPENOROFF_DEBUG){

    }
    else if(_eventType == EventType.CHANGE_SPINE_LOOP){
      skeleton.loop = data.param.loop;
    }

  }


  loadRemoto () {
    let sp = this.sp;
    let base = 'http://127.0.0.1/image/Socre_CandyMonsters_Scatter.png';
    assetManager.loadRemote(base, (err, asset: ImageAsset) => {
      if (err) return cc.error(err);
      console.log('asset:',asset)

      let texture = new Texture2D();
      texture.image = asset;
      // sp.spriteFrame = new SpriteFrame();
      // sp.spriteFrame.setTexture(texture)
      // sp.spriteFrame._texture = texture
      // sp.spriteFrame._texture = texture.getHtmlElementObj()
      sp.spriteFrame.texture = texture
    })
  }



  private addSpine () {
    // let spineNode = new Node();
    // spineNode.name = 'newSpine';
    // spineNode.setPosition(0,0);
    // var skeleton = spineNode.addComponent(sp.Skeleton);
    // // skeleton.parent = this.node;
    // cc.find("Canvas").addChild(spineNode);

    let skeleton = this.skeleton

    let asset = new sp.SkeletonData();
    asset.skeletonJson = this.spineJson;
    asset.atlasText = this.spineAtlas;
    asset.textures = [this.spineTexture];
    asset.textureNames = ['test_guai_001.png'];


    skeleton.skeletonData = asset;
    skeleton.animation = 'beiji';
    //skeleton._updateSkeletonData();
  }


  onLoadSpine2() {
    // let comp = this.getComponent('sp.Skeleton') as sp.Skeleton;
    // var spineNode = new cc.Node();
    // spineNode.name = 'newSpine';
    // spineNode.setPosition(49, -237);
    // var skeleton = spineNode.addComponent(sp.Skeleton);
    // cc.find("Canvas").addChild(spineNode);

    let skeleton = this.skeleton

    let image = "http://127.0.0.1/test_guai_001.png";
    let ske = "http://127.0.0.1/test_guai_001.json";
    let atlas = "http://127.0.0.1/test_guai_001.atlas";
    assetManager.loadAny([{ url: atlas, ext: '.txt' }, { url: ske, ext: '.txt' }], (error, assets) => {
        assetManager.loadRemote(image, (error, imageAsset:ImageAsset) => {

            let texture = new Texture2D();
            texture.image = imageAsset;


            let asset = new sp.SkeletonData();
            asset.skeletonJson = assets[1];
            asset.atlasText = assets[0];
            asset.textures = [texture];
            asset.textureNames = ['test_guai_001.png'];
            
            skeleton.skeletonData = asset;
            skeleton.animation = 'beiji';
        });
    });
  }



  onDisable () {
    EventManager.getInstance().off('machine',this.onEvent,this);
  }

}